@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light+Two&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    scroll-behavior: smooth;
}

.homepage {
    padding-top: 2vw;
    text-align: center;
}

.homepage h1 {
    font-size: 4vw;
    padding-bottom: 2vw;
    padding-left: 0;
    padding-right: 0;
    color: #1E56A7;
}

.homepage h2 {
    padding-top: 2vw;
    padding-bottom: 2vw;
    font-size: 2.5vw;
}

.homepage h3 {
    padding-top: 2vw;
    font-size: 2vw;
}

.image{
    width: 50vw;
    margin: 2vw auto;
}

.main-text {
    width: 60%;
    margin: auto;
    font-size: 1.6vw;
    font-weight: 700;
    margin-top: 1vw;
    padding-bottom: 2vw;
}

.diagram {
    margin-top: 1vw;
    border-radius: 1vw;
}

.card-holder{
    margin-top: 9vw;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 3vw;
    gap: 3vw;
    margin-bottom: 1vw;
}

.card {
    background-color: red;
    position: relative;
    width: 32vw;
    height: 13vw;
    text-align: start;
    padding-top: 1vw;
    padding-left: 1.5vw;
    padding-bottom: 1vw;
    border-radius: 1vw;
    color: #FCFCFF;
    box-shadow: 0px -2px 6px #0000005F;
}

.card button {
    padding: 0.8vw 2.7vw;
    border-radius: 0.5vw;
    border: none;
    margin-top: 1.2vw;
    font-size: 1vw;
    cursor: pointer;
    text-transform: uppercase;
    position: relative;
    bottom: 0;
    color: #0A122A;
    font-weight: 500;
}

.card button:hover {
    box-shadow: 0px -2px 6px #0000005F;
    bottom: 0.1vw;
}

.card-1 {
    background-color: #698F3F;
}


.card-2 {
    background-color: #804E49;
}

.card-heading {
    font-size: 2.3vw;
    font-weight: 700;
    margin-bottom: 1.5vw;
}

.card-text {
    width: 50%;
    font-size: 1.3vw;
}

.lady-1{
    width: 15vw;
    position: absolute;
    top: -7.8vw;
    right: 0;
}

.lady-2{
    width: 23vw;
    position: absolute;
    top: -8.6vw;
    right: -3vw;
}

.feature-holder {
    /* width: 60%; */
    margin: auto;
    display: flex;
    justify-content: center;
    align-content: center;
    grid-gap: 1.5vw;
    gap: 1.5vw;
    margin-bottom: 2vw;
}

.feature {
    padding: 1.5vw 3vw;
    border-radius: 0.7vw;
    box-shadow: 0px -2px 6px #0000005F;
    color: #0A122A;
    width: 12%;
    position: relative;
    bottom: 0;
}

.feature-heading {
    font-size: 1.4vw;
    font-weight: 500;
}

.feature-text {
    margin-top: 1vw;
    font-size: 1vw;
    font-weight: 400;
}

.feature:hover{
    bottom: 0.1vw;
}

.footer {
    font-size: 1.2vw;
    margin-bottom: 2vw;
    font-weight: 700;
    color: #804E49;
}
.nav {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 1vw;
    padding-bottom: 1vw;
}


.nav-link {
    font-size: 4vw;
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: 900;
    color: #1E56A7;
    text-decoration: none;
    text-shadow: 0px 1px 10px rgba(27,65,220,0.6);
}

.item {
    margin: auto 1.5vw;
}

.link {
    font-size: 1.2vw;
    color: #0a122a;
    font-weight: 500;
    position: relative;
    top: 0;
    text-decoration: none;
}

.link:hover{
    color: #1E56A7;
    top: -2px;
}

.item button {
    padding: 0.3vw 1.8vw;
    font-size: 1.2vw;
    color: #FCFCFF;
    font-weight: 500;
    border-radius: 0.3vw;
    background-color: #698f3f;
    border: none;
    cursor: pointer;
}

.item button:hover{
    background-color: #1E56A7;
}
.special-features {
    text-align: center;
}


.special-features .heading {
    font-size: 2.2vw;
    font-weight: 700;
    padding-bottom: 2vw;
}

.features-holder {
    width: 80%;
    display: flex;
    padding-left: 3vw;
    padding-right: 3vw;
    padding-top: 1.8vw;
    padding-bottom: 2vw;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.holder-text {
    width: 60vw;
    padding: 1vw;
}

.holder-image {
    width: 40vw;
}

.special-features h2 {
    padding-bottom: 2vw;
    font-size: 2vw;
}

.text {
    font-size: 1.2vw;
    font-weight: 500;
}
.about-us {
    text-align: center;
}

.heading {
    padding-top: 5vw;
    font-size: 3vw;
    font-weight: 700;
}

.team {
    display: flex;
    padding-top: 4vw;
    justify-content: center;
    grid-gap: 2vw;
    gap: 2vw;
    align-items: center;
    width: 90%;
    margin: auto;
}

.team-member {
    padding: 2vw;
    border-radius: 1vw;
    width: 25%;
}

.team-member img {
    border-radius: 1vw;
}

.team-member .name {
    text-transform: uppercase;
    padding: 1vw;
    padding-bottom: 0;
    font-size: 1.5vw;
    font-weight: 700;
}

.team-member .r-no {
    font-size: 1.2vw;
    padding-top: 0.3vw;
    font-weight: 500;
}
.contact-us-text {
    font-size: 3.5vw;
    padding-top: 2vw;
    text-align: center;
    font-weight: 700;
    color: #528681;
}

.contact-us img {
    width: 30vw;
}

.holder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin: auto;
    padding-left: 5vw;
    padding-top: 2vw;
}

.holder .form {
    padding-left: 3vw;
    padding-right: 3vw;
    width: 30vw !important;
    border-radius: 1vw;
}

.input-holder {
    display: flex;
    flex-direction: column;
    grid-gap: 0.8vw;
    gap: 0.8vw;
    padding: 0.6vw ;
    font-weight: 500;
}

textarea {
    resize: none;
    outline: none;
    padding: 0.5vw;
    border-radius: 0.5vw;
}

input {
    width: 70%;
    outline: none;
    padding: 0.5vw;
    border-radius: 0.5vw;
    border-width: thin;
}

.button {
    padding-bottom: 1vw;
}

.contact-us button {
    width: 100%;    
    height: 3vw;
    font-size: 1.2vw;
    font-weight: 500;
    background-color: #FF9C85;
    border: none;
    color: #fff;
    text-transform: uppercase;
    border-radius: 0.2vw;
    cursor: pointer;
}

/* .contact-us button:focus {
    background-color: #013C64;
} */

.inputs {
    display: flex;
    flex-direction: column;
    grid-gap: 1vw;
    gap: 1vw;
    padding-top: 3vw;
    padding-bottom: 3vw;
}
.signIn {
    padding-top: 7vw;
    margin: auto;
    text-align: center;
}

.input {
    padding-top: 3vw;
    padding-bottom: 2vw;
    display: flex;
    width: 30vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.signIn .button {
    width: 30vw;
    padding: 1vw 2vw;
    font-weight: 500;
    font-size: 1.3vw;
    background-color: #698f3f;
    outline: none;
    border: none;
    color: #FCFCFF;
    cursor: pointer;
}

.signIn .button:hover{
    background-color: #1E56A7;
}

.signUp-text {
    color: #1E56A7 ;
    font-weight: 500;
    padding: 1vw;
}

.signUp-text .text {
    color: #698f3f ;
    font-weight: 500;
    text-decoration: none;
    font-size: 1.1vw;
}
.signUp {
    text-align: center;
    padding-top: 4vw;
    width: 30vw;
    margin: auto;
}

.signUp .input {
    display: flex;
    flex-direction: column;
    grid-gap: 1vw;
    gap: 1vw;
}

.signUp .button {
    width: 30vw;
    padding: 1vw 2vw;
    font-weight: 500;
    font-size: 1.3vw;
    background-color: #698f3f;
    outline: none;
    border: none;
    color: #FCFCFF;
    cursor: pointer;
}

.signUp .button:hover{
    background-color: #1E56A7;
}

.signIn-text {
    color: #1E56A7 ;
    font-weight: 500;
    padding: 1vw;
}

.signIn-text .text {
    color: #698f3f ;
    font-weight: 500;
    text-decoration: none;
    font-size: 1.1vw;
}

.MuiOutlinedInput-input{
    cursor: pointer;
}
